import React, { Component } from 'react'
import StateType from "@/components/stateType/"
import TableBox from "@/components/table/index.jsx"
import "./style/newspapers.css"
import Bread from "@/components/Bread/"

class Newspapers extends Component {
    render() {
        return (
            <div className="newspapers-box">
                <Bread history={this.props.history}/>
                <StateType sortOpen={true} />
                <div className="content">
                    <TableBox />
                </div>
            </div>
        )
    }
}


export default Newspapers;